@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@200;300;400;500;600;700;800&display=swap);
*{margin:0;padding:0;box-sizing:border-box;scroll-behavior:smooth}body{background-color:#00020f}.loading-wrapper{position:absolute;top:0;z-index:999;display:flex;justify-content:center;align-items:center;width:100%;height:100vh;background-color:#00020f;transition:1.2s all ease}#root{width:100%;min-height:100vh}#root::after{position:absolute;content:'';right:0;width:82%;height:81vh;z-index:-1;top:0;background:linear-gradient(to top, #00020f 5%, transparent),linear-gradient(to right, #00020f 1%, transparent)}.movie-bg{position:absolute;right:0;z-index:-3 !important;width:80%;transition:1.5s opacity ease-in;height:80vh}.movie-bg-2{position:absolute;right:0;z-index:-2 !important;width:80%;transition:1.5s opacity ease-in;height:80vh}.my-movie-list-cont{display:flex;flex-direction:column;align-self:center;position:fixed;z-index:8000;right:-100rem;top:0;height:100vh;color:white;opacity:1;width:400px;background-color:#00041bef;transition:0.5s all ease-in-out}.my-list-title{width:85%;display:flex;justify-content:space-between;margin:20px auto;font-weight:200;font-size:1.5rem;font-family:var(--myfont)}.close-btn{cursor:pointer;margin-top:5px;transition:0.2s all ease-in-out}.close-btn:hover{font-size:1.2;color:red}.empty-list{font-weight:200;font-size:1rem;font-family:var(--myfont);margin:0 auto;text-align:center;width:80%}.my-list-all-movies{width:90%;margin:0 auto;display:grid;grid-template-columns:repeat(2, 1fr);overflow:scroll}.my-list-all-movies::-webkit-scrollbar{height:0px;width:0px}.each-movie{position:relative;margin:10px;transition:0.5s transform cubic-bezier(0.19, 1, 0.22, 1)}.each-movie:hover{box-shadow:0 0 12px rgba(224,8,224,0.5)}.each-movie:hover>.remove-wrapper{opacity:1}.each-movie-poster{cursor:pointer;width:100%}.remove-wrapper{cursor:pointer;opacity:0;position:absolute;display:flex;justify-content:center;top:0px;right:0px;padding:5px;background-color:rgba(0,0,0,0.5);transition:0.2s all ease}.remove-btn{font-size:1.5rem}.remove-wrapper:hover{background-color:rgba(224,8,8,0.701)}.my-list-btn-cont{width:100%;height:100px;margin:10px 0}.clear-list{position:absolute;bottom:1.5rem;left:50%;transform:translateX(-50%);cursor:pointer;font-family:var(--myfont);font-weight:500;font-size:1rem;color:white;width:50%;padding:7px 10px;background:#00020f;border:1px solid white;border-radius:5px;transition:0.5s all cubic-bezier(0.19, 1, 0.22, 1);outline:none}.clear-list:hover{background-color:white;color:#00020f}.footer{color:white;display:flex;justify-content:center;align-items:center;width:100%;background-color:#00020f;font-family:var(--myfont);margin:20px 0}.footer>a{margin-left:5px;color:goldenrod}@media screen and (max-width: 800px){.my-movie-list-cont{width:100%}.remove-wrapper{cursor:pointer;opacity:1;padding:0px;background-color:rgba(224,8,8,0.701)}.remove-btn{font-size:1rem}}

*{--myfont: 'Heebo', sans-serif}.nav-bar{display:flex;width:96%;color:white;margin:0 auto;margin-bottom:20px}.logo{width:150px;height:100%;margin-left:10px;margin-top:20px}.my-watchlist-cont{font-family:var(--myfont);display:flex;align-items:center;margin-left:20px;margin-right:10px;margin-top:20px;cursor:pointer}.watch-list-icon{font-size:2.5rem}.watchlist-text{font-size:1.5rem;margin-left:10px;margin-top:5px}.searchbox-cont{position:relative;z-index:10;width:100%;padding-top:20px;display:flex;flex-direction:column;align-items:center;justify-content:center;font-family:var(--myfont);margin:0 auto}.input-cont{position:relative;margin-top:1rem;margin:0 auto;width:100%;display:flex;justify-content:center;transition:1s all ease-in-out}.search-icon{position:absolute;top:11px;left:12px;font-size:1.3rem;color:rgba(255,255,255,0.356);transition:0.3s all ease-in-out}.searchbox{width:100%;padding:8px 5px;padding-left:2.5rem;border:1px solid rgba(255,255,255,0.1);outline:none;font-size:1.3rem;border-radius:3px;box-shadow:0px 0px 10px rgba(0,0,0,0.2);background:rgba(255,255,255,0.1);color:#c2c2c2;transition:0.3s all ease-in-out}.searchbox:focus{color:#000}.searchbox:focus{background:white;border:1px solid white}.input-cont:focus-within>.search-icon{color:#00020f}.suggestion-cont{position:absolute;z-index:3;top:68px;overflow:hidden;font-size:1.3rem;background-color:rgba(0,0,0,0.8);border-top:none;width:100%;color:white}.suggestions-list-movie{cursor:pointer;padding:10px 1rem}.suggestions-list-movie:hover{background-color:rgba(255,255,255,0.3)}.suggested-title{color:white;font-family:var(--myfont);font-weight:500;font-size:1.1rem;margin-bottom:10px;width:93%;margin:0 auto;transition:1.5s all cubic-bezier(0.445, 0.05, 0.55, 1)}.all-display-movie-wrapper{position:relative;width:100%;margin:0 auto;display:flex;align-items:center;justify-content:center;padding:18px 0;transition:1s height ease-in-out;margin-bottom:4px;overflow:hidden}.all-display-movie-cont{position:relative;width:93%;min-height:200px;z-index:10;bottom:-60rem;display:grid;grid-template-columns:repeat(auto-fit, minmax(180px, 1fr));grid-gap:20px;grid-auto-rows:1fr;transition:1.5s bottom cubic-bezier(0.445, 0.05, 0.55, 1),1.5s opacity ease;margin:0 auto;margin-bottom:10px}.no-movies{color:#fff;font-family:var(--myfont)}.suggested-movie{cursor:pointer;width:100%;background-color:rgba(21,0,34,0.927);object-fit:contain;transition:all 0.2s cubic-bezier(0.5, 0.25, 0, 1)}.suggested-movie:hover{transform:scale(1.08)}@media screen and (max-width: 1000px){.nav-bar{position:relative;display:flex;flex-direction:column;align-items:left}.logo{width:120px;margin-top:10px;margin-left:0px}.my-watchlist-cont{position:absolute;top:-10px;right:0}.watch-list-icon{font-size:1.5rem}.watchlist-text{font-size:1.3rem;margin-left:10px;margin-top:5px}.searchbox-cont{padding-top:10px}.searchbox{padding-left:2rem;font-size:1.05rem}.search-icon{position:absolute;top:9px;left:9px;font-size:1.1rem;color:rgba(255,255,255,0.349)}}@media screen and (max-width: 550px){.nav-bar{width:93%;margin-bottom:10px}.all-display-movie-cont{grid-template-columns:repeat(auto-fill, minmax(120px, 1fr));margin-bottom:10px}.suggested-movie:hover{transform:none;box-shadow:none}.suggestion-cont{top:48px}}@media only screen and (min-width: 1800px){.nav-bar{width:94%;margin-bottom:10px}}@media only screen and (min-width: 1500px){.suggested-movie:hover{transform:scale(1.03);box-shadow:0 0 50px -30px rgba(161,10,255,0.927)}}

.main-movie-cont{margin:0 auto;margin-top:15px;z-index:10;width:95%;display:flex;color:white;margin-bottom:10px}.left-main-movie-poster{width:15vw;min-width:260px;max-width:550px;margin-left:10px;outline:none;transition:1s all ease-in-out}.main-poster{width:100%;object-fit:contain;opacity:0;transition:1s height cubic-bezier(0.165, 0.84, 0.44, 1),1s opacity ease-in-out}.right-main-movie-details{position:relative;margin-left:1.5rem;width:40%;font-family:var(--myfont);display:flex;flex-direction:column;justify-content:flex-start;opacity:0;transition:0.8s opacity ease-in-out}.main-movie-title{font-size:2.5rem;font-weight:500;margin-bottom:10px;line-height:2.7rem;transition:1s all cubic-bezier(0.39, 0.575, 0.565, 1)}.main-movie-des{font-weight:300;font-size:1.1rem;margin-bottom:15px}.main-movie-details{margin-bottom:10px}.more-des{display:none}.read-more{color:grey;cursor:pointer;transition:0.4s color ease-in-out}.read-more:hover{color:goldenrod}.add-to-list{position:relative;margin-top:5px}#add-to-list-icon{position:absolute;font-size:1.5rem;color:rgba(255,255,255,0.3);margin-top:-5px;cursor:pointer;outline:none;transition:0.3s all ease-in-out}#add-to-list-input{display:none}#add-to-list-icon:hover{color:#ff5d5d}#add-to-list-input:checked+#add-to-list-icon{color:red;-webkit-animation:heart 1s cubic-bezier(0.17, 0.89, 0.32, 1.49);animation:heart 1s cubic-bezier(0.17, 0.89, 0.32, 1.49)}@-webkit-keyframes heart{0%,17.5%{transform:scale(1.3)}}@keyframes heart{0%,17.5%{transform:scale(1.3)}}.add-to-list-title{pointer-events:none;font-size:1.2rem;margin-left:35px;font-weight:200}.fa-star{font-size:1rem;margin-right:5px;color:gold}@media only screen and (max-width: 1000px){.left-main-movie-poster{min-width:200px;margin-left:0px;margin-bottom:10px;margin-top:2px}.main-movie-cont{flex-direction:column}.right-main-movie-details{width:100%;margin-left:0rem}.main-movie-title{font-size:2rem;font-weight:500;margin-bottom:5px;line-height:2.7rem}.main-movie-des{font-weight:300;width:100%;text-align:justify;font-size:1rem;margin-bottom:15px}.add-to-list{margin-bottom:10px}}@media only screen and (max-width: 550px){.main-movie-cont{width:92%}.main-movie-title{font-size:2rem;margin-bottom:5px}.main-movie-des{font-weight:300;font-size:0.9rem;margin-bottom:15px}.main-movie-details{margin-bottom:10px}}@media only screen and (min-width: 1800px){.main-movie-cont{width:94%}}

